import './news.sass'

import {New} from './New'

import newFoto1 from '../../img/new-1.webp'
import newFoto2 from '../../img/new-2.webp'
import newFoto3 from '../../img/new-3.webp'
import newFoto4 from '../../img/new-4.webp'
import miniLogo from '../../img/circle-logo.png'
import decor from '../../img/decor-2.webp'



function News(){

	const state = {
		news: [
			{newFoto: newFoto1, newTitle: ' ', newText: '       '},
			{newFoto: newFoto2, newTitle: ' ', newText: '       '},
			{newFoto: newFoto3, newTitle: ' ', newText: '       '},
			{newFoto: newFoto4, newTitle: ' ', newText: '       '}
		]
	}

	return(
		<section className="news">
			
			<div className="center-box">
				<h1 className="title news__title"> </h1>
			</div>
			<div className="news__items">
				{
					state.news.map((item, i) => {
						return(
							<New foto={item.newFoto} title={item.newTitle} text={item.newText} key={i} />
						)
					})
				}
			</div>
			<div className="center-box">
				<a className="button-red news__more" href="/"> </a>
			</div>
			<img class="news__decor decor" src={decor} alt="decor"/>
			<img className='mini-logo' src={miniLogo} alt="logo" />
		</section>
	)
}

export default News