

import './info.sass'

import banner from '../../img/banner.webp'


function Info() {

	const listDatas = [' ',' ','    ', '   ', '    ']
	const listItems = listDatas.map((item, i) =>
		<li key={i}><a href="/">{item}</a></li>
	)

	const listDatas2 = ['    ','   ', '          ']
	const listItems2 = listDatas2.map((item, i) =>
		<li key={i}><a href="/">{item}</a></li>
	)

	return (
		<section className="info">
			
			<div className="center-box">
				<h3 className="title info__title">     </h3>
			</div><img className="info__img" src={banner} alt="banner" />
			<div className="info__content content-info">
				<div className="content-info__col">
					<div className="content-info__title elem-title">  </div>
					<p> - ,     - .  2002 .    , , , , ,   , ,          .         ,   , -   ,  ,   .    :</p>
					<ul>
						{listItems}
					</ul>
				</div>
				<div className="content-info__col">
					<div className="content-info__title elem-title">  </div>
					<p>      .      :</p>
					<ul>
					{listItems2}
					</ul>
				</div>
				<div className="content-info__col">
					<div className="content-info__title elem-title">  </div>
					<p>   1   ,   .      .         -         .   .        .     .</p>
				</div>
			</div>
		</section>
	)
}

export default Info